header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*cta*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*socials*/
.header__social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__social::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* img */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;   
    overflow: hidden;
}

/* scroll down */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/*Medium devices (tablets)*/
@media screen and (max-width:1024px) {
    header{
        height: 68vh;
    }
}

/*small devices (mobile phones)*/
@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
    .header__social, .scroll__down{
        display: none;
    }
}